@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}body{background-color:#314152;font-family:"JetBrains Mono", monospace}ul{list-style:none;margin:0 !important}a{text-decoration:none !important;display:inline-block}img{max-width:100%}button:focus{outline:none}h1,h2,h3,h4,h5,h6{color:#fff;margin:0}p{font-size:14px;line-height:26px;margin:0}.titlecard{color:#ffffff;font-size:5em;font-family:"Permanent Marker", sans-serif;margin:1ex;text-align:center}.about-me{color:#ffffff;font-size:1em;font-family:"JetBrains Mono", monospace;margin:1ex;text-align:center}.c-social{list-style:none;margin-bottom:0;padding-left:0}img.center{border-radius:15% 15% 15% 15%;box-shadow:1px 1px 10px;display:block;max-width:400px;margin-left:auto;margin-right:auto;margin-bottom:50px}.container{width:100%;max-width:1140px;padding:0 15px;box-sizing:border-box;margin:0 auto}.timelines h2{text-align:center;color:#fff;font-weight:600;margin-bottom:40px;font-size:32px}.d-flex-2{display:flex;align-items:center}.d-flex-2:hover .t-square{animation:square-to-circle 0.5s 1 cubic-bezier(1, 0.015, 0.295, 1.225);border-radius:100% 100% 100% 100%;background:#dec85b}.timeline-area{padding:80px 0}.all-timelines{position:relative}.timelines h2{text-align:center;color:#fff;font-weight:600;margin-bottom:40px}.all-timelines::before{content:"";position:absolute;left:0;right:0;margin:auto;height:100%;width:2px;background:#75c23f;top:20px}.single-timeline{margin-bottom:22px}.timeline-blank{width:50%}.timeline-text{width:50%;padding-left:30px;box-sizing:border-box;position:relative}.timeline-text h6{color:#f0f1f2;font-weight:900;display:inline-block;font-size:1rem}.timeline-text span{color:#f0f1f2;display:block;width:100%}.timeline-text-title{float:right;width:50%;text-align:right;padding-left:0;padding-right:30px;box-sizing:border-box;position:relative}.single-timeline:nth-child(even) .timeline-text-title{float:left;width:50%;text-align:left;padding-left:30px;padding-right:0px;box-sizing:border-box;position:relative}.timeline-text-title h6{color:#f0f1f2;font-weight:900;display:inline-block;font-size:1rem}.t-square{content:"";position:absolute;width:12px;height:12px;left:-6px;background:#75c23f;animation:circle-to-square .5s 1;transition:background 0.3s ease-in-out}.single-timeline:nth-child(even) .timeline-text span{text-align:right}.single-timeline:nth-child(even){flex-direction:row-reverse}.single-timeline:nth-child(even) .t-square{right:-6px;left:unset}.single-timeline:nth-child(even) .timeline-text{padding-left:0;padding-right:30px;text-align:right}@keyframes square-to-circle{0%{border-radius:0 0 0 0;background:#75c23f;transform:rotate(0deg)}100%{border-radius:100% 100% 100% 100%;background:#dec85b;transform:rotate(360deg)}}@keyframes circle-to-square{0%{border-radius:100% 100% 100% 100%;background:#dec85b;transform:rotate(360deg)}100%{border-radius:0 0 0 0;background:#75c23f;transform:rotate(0deg)}}@media all and (max-width: 768px){.all-timelines::before{right:unset;top:0}.single-timeline:nth-child(2n) .timeline-text{padding-left:30px;padding-right:0;text-align:left}.single-timeline:nth-child(2n) .t-square{left:-6px;right:unset}.timeline-blank{display:none}.timeline-text{width:100%}.single-timeline:nth-child(even) .timeline-text span{text-align:left !important}}@media all and (max-width: 360px){.all-timelines::before{top:32px}}

